body {
  --font-color: #2f4f4f;
  --rank-color: #8b0000;
  --hover-color: #eee;
  --description-color: #000;
  text-align: center;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

* {
  box-sizing: border-box;
}

.people {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  align-items: center;
  gap: 30px 40px;
  margin-top: 30px;
  display: grid;
}

@media (width <= 500px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

.person {
  transition: transform .5s ease-in-out, background-color .5s ease-in-out, display .5s ease-in-out, visibility 1s ease-in-out;
  position: relative;
}

.person:hover {
  z-index: 2;
  background-color: var(--hover-color);
  border-radius: 12px;
  max-height: 314px;
  margin: -12px;
  padding: 12px;
  transform: scale(1.2);
}

.person:hover .image {
  border-radius: 60px;
}

.person:hover .sport, .person:hover .pay {
  opacity: 1;
  visibility: visible;
  border-radius: 60px;
}

.image {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: border-radius 1s;
  display: block;
}

.rank {
  color: var(--rank-color);
  background-color: #fff;
  border-radius: 200px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: bold;
  display: flex;
  position: relative;
  bottom: 114px;
  left: 158px;
}

.name {
  text-align: center;
  color: var(--font-color);
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
  position: relative;
  bottom: 20px;
}

.extra {
  justify-content: center;
  display: flex;
  position: relative;
}

.sport, .pay {
  opacity: 0;
  visibility: hidden;
  text-align: center;
  color: var(--description-color);
  font-family: Arial;
  font-size: 18px;
  transition: border-radius .5s ease-in-out, opacity .5s ease-in-out;
  position: relative;
  bottom: 10px;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: " $";
}

.pay:after {
  content: "M";
}
